/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: 1200px) {
    @content;
  }
}

@mixin big-tablet {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
