.form {
  margin-top: 30px;
  display: grid;
  grid-template: 45px 190px 45px / repeat(2, 280px);
  justify-content: space-between;
  row-gap: 30px;
  &__group {
    position: relative;
  }
  &__label {
    position: absolute;
    display: block;
    height: 18px;
    top: -9px;
    left: 12px;
    padding: 0 8px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #000000;
    background-color: #fff;
    z-index: 1;
    &-check {
      position: relative;
      padding-left: 25px;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #000000;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform:translateY(-50%);
        width: 18px;
        height: 18px;
        border: 1px solid #000;
        border-radius: 4px;
        /*&:checked {
          background-color: var(--main-color);
        } */
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform:translateY(-50%);
        display: block;
        width: 18px;
        height: 18px;
        border-radius: 4px;
        background-position: center;
        background-size: cover;
      }
    }
  }
  &__input {
    padding: 0 20px;
    width: 100%;
    height: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #000000;
  }
  &__textarea {
    position: relative;
    grid-column: 1/3;
    textarea {
      width: 100%;
      height: 100%;
      padding: 20px;
      resize: none;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-column: 1/3;
  }

  &__checkbox {
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 4px;
    opacity: 0;
    z-index: 1;
    &:checked + label::after {
      background-image: url('../img/svg/check.svg');
      background-color: var(--main-color);
      border: none;
    }
  }
}
