.footer {
  padding: 15px 0;
  background-color: #eee;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__policy {
    display: block;
    font-size: 14px;
    color: #000;
    &:hover {
      text-decoration: underline;
    }
  }
}

.social {
  margin-top: 10px;
  display: flex;
  align-items: center;
  svg {
    width: 30px;
    height: 30px;
  }
  &__item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
