.portfolio {
  padding-top: 120px;
  &__subtitle {
    margin-top: 15px;
  }
  &__title {
    position: relative;
    @include circle;
    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__wrapper {
    margin-top: 60px;
    padding-bottom: 30px;
    display: grid;
    grid-template-columns: repeat(4, 280px);
    grid-auto-rows: 280px;
    gap: 5px;
  }

  &__item {
    position: relative;
    display: block;
    cursor: pointer;
    transition: transform 0.4s;
    &-title {
      color: #fff;
    }
    &-text {
      margin-top: 20px;
    }
    &-inner {
      position: absolute;
      opacity: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 15px;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #fff;
      transition: opacity 0.4s;
      background-color: rgba(0, 0, 0, 0.7);
      &:hover {
        opacity: 1;
      }
    }
/*     &--vertical {
      grid-row: 1/3;
      grid-column: 4/5;
    }
    &--horizontal {
      width: 100%;
      height: 100%;
      grid-column: 2/4;
      grid-row: 2/3;
    } */
    &:hover {
      transform: scale(1.015);
    }

    .image {
      height: 100%;
    }
  }
}
