@include for-desktop {
  .container {
    max-width: 960px
  }
  .sidepanel {
    display: none;
  }

  .about {
    &__inner {
      grid-template-columns: 400px 520px;
      column-gap: 40px;
    }
    &__advantages {
      grid-template: repeat(2, minmax(115px, auto), repeat(2, 250px));
      column-gap: 20px;
    }
    .advantages {
      &__item {
        column-gap: 10px;
        grid-template-columns: 40px 195px;
      }
    }
  }

  .resume {
    &__wrapper {
      padding: 0;
      margin-top: 60px;
      grid-template-columns: repeat(2, 415px);
      column-gap: 100px;
    }
  }

  .skills {
    &__wrapper {
      grid-template-columns: repeat(auto-fit, 270px);
      justify-content: center;
    }
  }

  .portfolio {
    &__wrapper {
      grid-template-columns: repeat(auto-fit, 280px);
      grid-auto-rows: 280px;
      justify-content: space-around;
      row-gap: 35px;
    }
    &__item {
      &--horizontal,
      &--vertical {
        grid-column: unset;
        grid-row: unset;
      }
      .image {
        width: 100%;
        height: 100%;
      }
    }
  }

  .contact {
    &__wrapper {
      padding: 0;
      grid-template-columns: 400px 520px;
    }
    &__form {
      grid-template: 45px 190px 45px / repeat(2, 250px);
    }
  }
}

@include big-tablet {
  .container {
    max-width: 720px
  }
  .title {
    &--fz36 {
      font-size: 30px;
    }
  }
  .about {
    &__inner {
      grid-template-columns: 200px 480px;
    }
  }

  .resume {
    &__wrapper {
      grid-template-columns: repeat(2, 350px);
      column-gap: 20px;
    }
  }

  .price {
    &__wrapper {
      grid-template-columns: repeat(auto-fit, 350px);
      justify-content: center;
      padding: 0;
      gap: 20px;
    }
  }
  .contact {
    &__wrapper {
      grid-template-columns: 300px 400px;
    }
    &__form {
      grid-template: 45px 190px 45px / repeat(2, 195px);
    }
  }
}

@include tablet {
  .container {
    max-width: 540px
  }
  .title {
    &--fz48 {
      font-size: 28px;
    }
  }
  .hero {
    &__title {
      width: 400px;
      line-height: 39px;
      font-size: 26px;

    }
  }
  .about {
    padding-top: 20px;
    &__inner {
      grid-template: 1fr auto auto/ 1fr;
    }
    &__img {
      margin: 0 auto;
    }
    .advantages {
      grid-template-columns: repeat(2, 260px);
      &__item {
        column-gap: 20px;
        grid-template-columns: 40px 200px;
      }
    }
  }
  .resume {
    padding-top: 40px;
    &__wrapper {
      margin-top: 40px;
      grid-template-columns: 1fr;
      row-gap: 40px;
      column-gap: 20px;
    }
  }
  .skills {
    padding-top: 40px;
    &__wrapper {
      row-gap: 20px;
    }
  }
  .portfolio {
    padding-top: 60px;
  }
  .price {
    padding-top: 40px;
    &__wrapper {
      margin-top: 20px;
      justify-content: center;
    }
  }
  .contact {
    padding-top: 40px;
    &__img {
      display: none;
    }
    &__wrapper {
      grid-template-columns: 1fr;
    }
    &__form {
      grid-template: 45px 190px 45px / repeat(2, 250px);
    }
  }
}

@include mobile {
  .container {
    max-width: 100%;
    padding: 0 10px;
  }
  .title {
    &--fz48 {
      font-size: 26px;
    }
    &--fz36 {
      font-size: 24px;
    }
  }
  .hero {
    &__hamburger {
      right: 20px;
    }
    &__content {
      padding-top: 120px;
    }
    &__btns {
      margin-top: 32px;
    }
  }

  .menu {
    &__block {
      padding-left: 20px;
      width: 250px;
    }
  }

  .about {
    &__subtitle {
      line-height: 36px;
    }
    &__title {
      margin-top: 20px;
    }
    &__inner {
      grid-template: 220px auto auto / 1fr;
    }
    .advantages {
      grid-template-columns: 1fr;
    }
  }

  .skills {
    &__wrapper {
      margin-top: 20px;
    }
  }

  .price {
    &__wrapper {
        grid-template-columns: repeat(auto-fit, 295px);
    }
  }

  .contact {
    padding-bottom: 50px;
    &__form {
      grid-template: 45px 45px 190px minmax(45px, auto) / 1fr
    }


    .form {
      &__textarea {
        grid-column: unset;
      }
      &__bottom {
        grid-column: unset;
        flex-direction: column-reverse;
      }
      &__btn {
        max-width: 220px;
        margin: 15px auto 0 auto;
      }
      &__policy {
        display: block;
        width: 100%;
        margin: 0 auto;
        line-height: 18px;
      }
    }
  }

  .footer {
    &__inner {
      flex-direction: column-reverse;
    }
    &__policy {
      margin-top: 10px;
    }
  }
}
