.about {
    padding-top: 120px;

    .divider {
        margin-left: 0;
    }
    &__inner {
        display: grid;
        grid-template-columns: 200px 670px;
        grid-auto-rows: minmax(200px);
        justify-content: center;
        column-gap: 70px;
    }

    &__img {
        grid-row: 1/3;
        border-radius: 5px;
        max-width: 200px;
        max-height: 200px;
        img {
          width: 100%;
          border-radius: 100%;
        }
    }

    &__subtitle {
        position: relative;
        padding-left: 12px;
        padding-top: 1px;
        @include circle;
    }

    &__content {
        padding-top: 8px;
        .divider {
            margin-top: 10px;
        }
    }

    &__title {
        margin-top: 10px;
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 21px;
        margin-top: 20px;
    }
}
